import { useEffect, useState } from 'react';
import { resumeAPI } from '../api/service';

/**
 * ResumeExistenceGate (render prop )
 * - (PDF)    fetch(HEAD) ,
 *    children(pdfUrl) , // fallback   UI 
 *
 * @param {(pdfUrl: string) => React.ReactNode} children - PDF    (children)
 * @param {ReactNode} fallback - PDF    /
 */
export default function ResumeExistenceGate({ children, fallback }) {
    const [status, setStatus] = useState("loading"); // 'loading' | 'exists' | 'notfound' | 'error'
    const [pdfUrl, setPdfUrl] = useState(null);

    useEffect(() => {
        async function checkResume() {
            try {
                const data = await resumeAPI.getResume();
                if (!data.pdf_url) {
                    setStatus("notfound");
                } else {
                    setStatus("exists");
                    const idx = data.pdf_url.indexOf("/public/");
                    if (idx !== -1) {
                        setPdfUrl("/" + data.pdf_url.substring(idx + "/public/".length));
                    } else {
                        setPdfUrl(data.pdf_url);
                    }
                }
            } catch (error) {
                setStatus("error");
            }
        }
        checkResume();
    }, []);

    if (status === "loading") return <div className="text-gray-400 mt-16">  ...</div>;
    if (status === "error") return <div className="text-red-500 mt-16">    .</div>;
    if (status === "notfound") return fallback;
    // exists
    return children(pdfUrl);
}
